<template>
    <div class="list clearfix">
        <div id="suggest1">
            <ul id="cartoon-suggest">
                <li v-for="(photo, index) in photoList" :key="index">
                    <router-link class="cartoon-img" :to="'/photo/photoDetail/'+index">
                        <img :src="photo.src" />
                    </router-link>             
                </li>
            </ul>
        </div>
        

        <ul class="pList">
            <li v-for="(photo, index) in photoList" :key="index">
                <router-link :to="'/photo/photoDetail/'+index">
                    <img :src="photo.src" />
                </router-link>
                
            </li>
        </ul>
    </div>
</template>

<script>
import Axios from 'axios'

export default {
    data(){
        return {
            photoList: []
        }
    },
    mounted(){
        let url = '/static/data/photodata.json'

        Axios.get(url).then(res => {
            this.photoList = res.data.photoData

            //将数据存入vuex
            this.$store.dispatch('actionPhotoList', this.photoList);
           // console.log(this.photoList);
        }).catch()
    }
}
</script>

<style scoped>
@import url('../../assets/css/common.css');
.list{ margin: 1rem 0;}

#suggest1{flex-wrap: nowrap;justify-content: space-evenly;overflow: auto; /*overflow-x: scroll*/}
#suggest1::-webkit-scrollbar{display: none;}
#cartoon-suggest{width:800%; display: flex;}/*width:百分比, 调整百分比:控制图片的大小*/
#cartoon-suggest .cartoon {width: 150%;margin-left: 0.1rem;}
#cartoon-suggest .cartoon .cartoon-img img{height: 2.5rem;width: 150%;} 

.list .scroll li{width: 50%; float:left;}
.list .scroll li img{width: 100%;}

.list .pList li{width: 50%; float:left;}
.list .pList li img{width: 100%;}
</style>




