<template>
    <div>
        <common-header bg="rgb(111,73,63)">
            <span slot="title">Book</span>
        </common-header>
        <swipe class="my-swipe">
            <swipe-item class="slide1"><h1 style="font-size:80px">one</h1></swipe-item>
            <swipe-item class="slide2">two</swipe-item>
            <swipe-item class="slide3">333</swipe-item>
        </swipe>
        <common-footer bg="rgb(111,73,63)"></common-footer>
        
    </div>
</template>

<script>
import CommonHeader from '../common/Header'
import CommonFooter from '../common/Footer'
import { Swipe, SwipeItem } from 'vue-swipe';
import 'vue-swipe/dist/vue-swipe.css';
/**
  一.import 文件中没有export,不能使用from:
 1. import 'reset.css' 相当于: <link rel="stylesheet" href="reset.css">
 2. import 'jqery.js' 相当于:  <script src="jquery.js">
  二. import 与 require区别:
  import "文件.扩展名": 是ES6引入模块的方法, vue中建议使用import
  require("文件.扩展名"): 是nodeJs引入模块的方法, nodeJs是平台,是环境. 
 */

export default {
    components: {
        CommonHeader,
        CommonFooter,
        'swipe': Swipe,
        'swipe-item': SwipeItem
    }
}
</script>

<style scoped>
.my-swipe {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.slide1 {
  background-image: url('https://img3.doubanio.com/lpic/s27102925.jpg');
  color: #fff;
}

.slide2 {
  background-image: url('https://img3.doubanio.com/lpic/s6989253.jpg');
  color: #000;
}

.slide3 {
  background-image: url('/static/img/a.jpg');
  color: #fff;
  background-size: 100%;
}
</style>




