<template>
    <div >
       <ul class="footer" :style="{background: bg}">
           <li>
               <router-link to="/movie/movieList" >电影</router-link>               
           </li>
           <li>
               <router-link to="/music" >音乐</router-link>               
           </li>
           <li>
               <router-link to="/book" >书藉</router-link>               
           </li>
           <li>
               <router-link to="/photo" >图片</router-link>               
           </li>
       </ul>
    </div>
</template>

<script>
export default {
    props: ['bg']
};
</script>

<style scoped>
.footer{
    width: 100%;
    height: 1rem;
    background:#2196f3;
    position: fixed;
    bottom:0;
    line-height: 1rem;
    display:flex;
    justify-content: space-evenly;
}

.footer li a{ color:#ccc; outline:none;}
.footer li a.router-link-active{color: #fff; } /*被点中的为白色*/
</style>




